<script>
import { ImagePreview } from 'vant'
export default {
  data() {
    return {
      allpage: -1,
      loading: false,
      finished: false,
      item: {
        token: this.$store.state.userData,
        type: "0",
        page_size: 10,
        page_index: 0
      },
      list: [],
      ImagePreviewDialog:''
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.answersItem();
    },
    //查看图片
    answersImageClick(image,index) {
      console.log(image)
      this.ImagePreviewDialog = ImagePreview({
        images:image,
        // startPosition: index
      })
    },
    //投票
    voteOwnClick(tid,optionid) {
      this.api.addVote({
        token:this.$store.state.userData,
        tid:tid,
        optionid:optionid
      }).then(res=> {
        console.log(res)
        if(res.err_code == 0 && res.data =='') {
          this.$toast('投票成功')
        }else if(res.data.status == -1){
          this.$toast(res.data.msg)
        }
      })
     },
    answersItem() {
      this.api.answersItem(this.item).then(res => {
        if (res.err_code == 0) {
          this.allpage = res.data.total_page;
          this.list = [...this.list,...res.data.forum_thread_data];
        }
        this.loading = false;
      });
    },
    onLoad() {
      if (this.list.length) {
        this.item.page_index++;
        this.answersItem();
      }
      if (this.item.page_index == this.allpage) {
        this.finished = true;
      }
    },
    //回答详情
    answersDetailsClick(id) {
      this.$router.push({ path: "/answers/answersDetails", query: { id: id } });
    }
  }
};
</script>
